 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Prop } from "vue-property-decorator";
import Url from "../api";
import { FormRules } from "@/components/elementUI/form";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class ChartsControlPenal extends Vue {
  @Prop({ type: String })
  title!: string;
  @Prop({ type: String })
  orgId!: string;

  componentStatus: boolean = true;
  chartItem: any = {};
  leftChartsList: any[] = [];
  rightChartsList: any[] = [];
  editMode: string = "add";

  formRules: FormRules = {
    name: { required: true, message: "图表名称不能为空", trigger: "blur" },
    position: { required: true, message: "位置不能为空", trigger: "change" },
    url: { required: true, message: "链接地址不能为空", trigger: "blur" }
  };

  created() {
    this.getChartsList();
  }

  addNewChart() {
    this.editMode = "add";
    this.componentStatus = false;
  }
  //
  async getChartsList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.settings.getChartsList}?orgId=${this.orgId}`);
      if (status) {
        console.log(data);
        this.leftChartsList = data.filter((i: any) => i.position === 0);
        this.rightChartsList = data.filter((i: any) => i.position === 1);
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // 移动tool-item
  async moveItems(id: string, flag: number) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.settings.moveChartsItems, {
        params: {
          flag: flag,
          chartId: id,
          orgId: this.orgId
        }
      });
      if (status) {
        this.$message.success("移动成功");
        this.getChartsList();
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // 获取自定义工具栏详情
  async getChartItemDetail(id: string) {
    this.editMode = "edit";
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.settings.getChartsItem}?chartId=${id}&orgId=${this.orgId}`);
      if (status) {
        // this.typeTreeData = data;
        this.chartItem = data;
        this.componentStatus = false;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // 删除自定义工具栏
  deleteChartItem(id: string) {
    this.$http.get(`${Url.settings.deleteChartsItem}?chartId=${id}&orgId=${this.orgId}`).then((res: any) => {
      if (res.data.status) {
        this.$message.success("删除成功！");
        this.getChartsList();
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 验证表单
  checkFormRules() {
    (this.$refs["chartForm"] as ElForm).validate((isValid: boolean) => {
      if (isValid) {
        if (this.chartItem.width && this.chartItem.height) this.saveCharItem();
        else this.$message.warning("请设置图表尺寸");
      }
    });
  }

  // 保存图表
  async saveCharItem() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(this.editMode === "add" ? Url.settings.addChartsItem : Url.settings.updateChartsItem, {
        ...this.chartItem,
        orgId: this.orgId
      });
      if (status) {
        this.$message.success("保存成功！");
        this.componentStatus = true;
        this.getChartsList();
        this.chartItem = {};
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  backToList() {
    this.componentStatus = true;
    this.chartItem = {};
  }
}
