 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Watch } from "vue-property-decorator";
import Url from "../api";
import MapControlPenal from "@/microApps/geographic/components/MapControlPenal.vue";
import ToolsControlPenal from "@/microApps/geographic/components/ToolsControlPenal.vue";
import ChartsControlPenal from "@/microApps/geographic/components/ChartsControlPenal.vue";
import { loadAMap } from "common-config/lib/utils/loadAmap";

@Component({
  components: { MapControlPenal, ToolsControlPenal, ChartsControlPenal }
})
export default class MapToolConfig extends Vue {
  orgName: string = "";
  // 组织机构树
  treeMessage: any = {
    filterText: "",
    data: []
  };
  // 配置选项
  defaultProps: any = {
    value: "id",
    label: "name",
    children: "children"
  };
  hasMap: boolean = false;
  showControlPenal: boolean = false;
  penalName: string = "";
  controls: { [key: string]: string } = {
    map: "地图配置",
    toolBar: "工具栏配置",
    charts: "图表配置"
  };
  witchComponent: string = "MapControlPenal";
  editMode: string = "add";

  mapConfig: any = {
    mapRegion: "amap"
  };
  userDefinedTools: any[] = [];
  chartsList: any[] = [];

  orgId: string = "";
  map: any;
  oldKey: string = "";

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.defaultProps.label].indexOf(value) !== -1;
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.orgTree as any).filter(val);
  }

  @Watch("penalName", { immediate: true })
  switchComponent() {
    if (!this.penalName) {
      return;
    }
    this.showControlPenal = true;
    switch (this.penalName) {
      case "map":
        this.witchComponent = "MapControlPenal";
        break;
      case "toolBar":
        this.witchComponent = "ToolsControlPenal";
        break;
      case "charts":
        this.witchComponent = "ChartsControlPenal";
        break;
      default:
        this.witchComponent = "MapControlPenal";
    }
  }

  //获取组织机构树
  getTreeData() {
    // this.pageLoading = true;
    this.$http.get(Url.organization.getOrgTreeData).then((res: any) => {
      // this.pageLoading = false;
      if (res.data.status) {
        this.treeMessage.data = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 树节点点击事件
  orgNodeClick(node: any) {
    this.orgName = node.name;
    this.orgId = node.id;
    this.showControlPenal = false;
    this.penalName = "";
    this.getMapConfig();
  }

  // 点击添加地图按钮打开配置penal
  openMapConfigPenal() {
    this.hasMap = true;
    // this.penalName = "map";
    // this.showControlPenal = true;
  }

  // hide配置penal
  hideMapConfigPenal() {
    this.hasMap = false;
    this.penalName = "";
    this.showControlPenal = false;
    this.mapConfig = {
      mapRegion: "amap",
      mapScale: 10,
      style: "normal"
    };
    this.changeMapStyle(this.mapConfig);
  }

  // 获取地图样式配置
  async getMapConfig() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.settings.getMapConfig, { params: { orgId: this.orgId } });
      if (status) {
        if (data) {
          this.mapConfig = data;
          // setTimeout(() => {
          this.changeMapStyle(this.mapConfig);
          this.openMapConfigPenal();
          // }, 1);
        } else {
          this.hideMapConfigPenal();
        }
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }

  // 加载地图
  initMap() {
    this.$nextTick(() => {
      this.map = new (window as any).AMap.Map("org-map", {
        // viewMode: "3D",
        center: [106.5, 29.5],
        zoom: 10,
        doubleClickZoom: false
      }).on("zoomchange", (e: any) => {
        this.mapConfig.mapScale = this.map.getZoom();
      });
    });
  }

  // 根据 配置项 修改地图样式
  changeMapStyle(config: any) {
    // if (!!config.key && config.key !== this.oldKey) {
    //   loadAMap(config.key);
    //   this.oldKey = config.key;
    // }
    if (config.style) {
      this.map.setMapStyle(`amap://styles/${config.style}`);
    }
    if (config.mapCenter) {
      this.mapConfig.mapCenter = config.mapCenter;
      this.map.setCenter(config.mapCenter.split(","));
    }
    if (config.mapScale) {
      this.map.setZoom(config.mapScale);
    }
  }

  // 隐藏配置penal
  closeControlPenal() {
    this.penalName = "";
    this.showControlPenal = !this.showControlPenal;
  }

  mounted() {
    this.getTreeData();
    this.initMap();
  }

  beforeDestroy() {
    this.map.destroy();
    this.map = null;
    console.log(this.map);
  }
}
