import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import Url from "../api";
import { FormRules } from "@/components/elementUI/form";
import { ElForm } from "element-ui/types/form";

const AMap: any = (window as any).AMap;

@Component({})
export default class MapControlPenal extends Vue {
  @Prop({ type: String })
  title!: string;
  @Prop({ type: String })
  orgId!: string;
  @Prop({ type: Object })
  map!: any;
  @Prop({ type: Object })
  mapConfig!: any;

  sources: { label: string; value: string }[] = [
    {
      label: "高德地图",
      value: "amap"
    }
  ];
  configs: any = this.mapConfig;
  mapClick: boolean = false;
  clickListener: any = null;
  formRules: FormRules = {
    mapRegion: { required: true, message: "请输入名称", trigger: "change" },
    style: { required: true, message: "样式不能为空", trigger: "blur" },
    mapCenter: { required: true, message: "中心点不能为空", trigger: "blur" }
  };
  mapPresetStyles: any[] = [
    {
      label: "标准",
      value: "normal"
    },
    {
      label: "幻影黑",
      value: "dark"
    },
    {
      label: "月光银",
      value: "light"
    },
    {
      label: "远山黛",
      value: "whitesmoke"
    },
    {
      label: "草色青",
      value: "fresh"
    },
    {
      label: "雅士灰",
      value: "grey"
    },
    {
      label: "涂鸦",
      value: "graffiti"
    },
    {
      label: "马卡龙",
      value: "macaron"
    },
    {
      label: "靛青蓝",
      value: "blue"
    },
    {
      label: "极夜蓝",
      value: "darkblue"
    },
    {
      label: "酱籽",
      value: "wine"
    },
    {
      label: "QT-dark",
      value: "c557b410d7210c8530b0269adba25f5c"
    }
  ];

  @Watch("orgId", { immediate: true })
  async handleChangeOrg() {}

  handleChange() {
    this.$emit("mapConfigChange", this.configs);
  }
  chooseCenterOnMap(status: boolean) {
    if (status) {
      AMap.event.removeListener(this.clickListener); //移除事件，以绑定时返回的对象作为参数
    } else {
      this.clickListener = AMap.event.addListener(this.map, "dblclick", (e: any) => {
        this.configs.mapCenter = e.lnglat.getLng() + "," + e.lnglat.getLat();
        this.$message.success(`您当前选择了[${this.configs.mapCenter}]作为地图中心点`);
        this.handleChange();
      });
    }
    this.mapClick = !this.mapClick;
  }

  saveMapConfig() {
    (this.$refs["mapForm"] as ElForm).validate((isValid: boolean) => {
      if (isValid) {
        this.$http
          .post(Url.settings.setMapConfig, {
            ...this.configs,
            orgId: this.orgId,
            mapKey: "fbc043611829448a8d799c0a5fef544a"
          })
          .then((success: any) => {
            if (success.data.status) {
              this.$message.success("保存成功！");
            } else {
              this.$message.error(success.data.message);
            }
          });
      }
    });
  }
}
