 * @Description: -
 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Prop } from "vue-property-decorator";
import Url from "../api";
import { FormRules } from "@/components/elementUI/form";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class ToolsControlPenal extends Vue {
  @Prop({ type: String })
  title!: string;
  @Prop({ type: String })
  orgId!: string;

  componentStatus: boolean = true;

  activeName: string = "systemData";
  checkedSystemData: string[] = [];
  checkedSystemTools: string[] = [];

  userToolsList: any = [];

  toolItem: any = {
    isShow: false,
    icons: [],
    typeDataIds: [],
    icon: ""
  };

  typeTreeData: any[] = [];
  typeTreeProps: any = {
    children: "children",
    label: "name"
  };
  dataLoading: boolean = false;
  editMode: string = "add";

  formRules: FormRules = {
    name: { required: true, message: "工具名称不能为空", trigger: "blur" },
    icons: { required: true, message: "图标不能为空", trigger: "change" },
    typeId: { required: true, message: "类型不能为空", trigger: "blur" }
  };

  addNewChart() {
    this.editMode = "add";
    this.componentStatus = false;
  }
  // 自定义工具栏列表
  async getUserToolsList() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.settings.getUserToolsList}?orgId=${this.orgId}`);
      if (status) {
        this.userToolsList = data;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // 移动tool-item
  async moveItems(id: string, flag: number) {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(Url.settings.moveUserToolsItems, {
        params: {
          flag: flag,
          toolbarId: id,
          orgId: this.orgId
        }
      });
      if (status) {
        this.$message.success("移动成功");
        this.getUserToolsList();
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // get data for checked type
  async getSelectedTypeTree(type: string) {
    this.dataLoading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.settings.findTypeTree}/${type}`);
      if (status) {
        this.typeTreeData = data;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    } finally {
      this.dataLoading = false;
    }
  }
  // 获取自定义工具栏详情
  async getToolItemDetail(id: string) {
    this.editMode = "edit";
    this.componentStatus = false;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.settings.getUserToolsItem}?toolbarId=${id}&orgId=${this.orgId}`);
      if (status) {
        // this.typeTreeData = data;
        this.toolItem = data;
        this.toolItem.toolbarId = id;
        this.toolItem.icons = [
          {
            name: "icon",
            url: data.icon
          }
        ];
        this.componentStatus = false;
        this.getSelectedTypeTree(data.typeId).then(() => {
          this.$nextTick(() => {
            (this.$refs["typeTree"] as any).setCheckedKeys(this.toolItem.typeDataIds);
          });
        });
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // 获取树节点选中数据
  checkedChange() {
    // console.log((this.$refs["typeTree"] as any).getCheckedNodes().map((i: any) => i.id));
    this.toolItem.typeDataIds = (this.$refs["typeTree"] as any).getCheckedNodes().map((i: any) => i.id);
  }
  // 保存自定义工具栏
  saveToolsItem() {
    let url: string = this.editMode === "add" ? Url.settings.addUserToolsItem : Url.settings.updateUserToolsItem;
    (this.$refs["toolForm"] as ElForm).validate((validate: boolean) => {
      if (validate) {
        if (this.toolItem.typeDataIds && this.toolItem.typeDataIds.length) {
          this.$http
            .post(url, {
              ...this.toolItem,
              icon: this.toolItem.icons[0].url,
              orgId: this.orgId
            })
            .then((res: any) => {
              if (res.data.status) {
                this.$message.success("保存成功！");
                this.getUserToolsList();
                this.backToList();
              } else {
                this.$message.error(res.data.message);
              }
            });
        } else {
          this.$message.warning("请选择至少一项数据类型");
        }
      }
    });
  }
  // 删除自定义工具栏
  deleteToolItem(id: string) {
    this.$http.get(`${Url.settings.deleteUserToolsItem}?toolbarId=${id}&orgId=${this.orgId}`).then((res: any) => {
      if (res.data.status) {
        this.$message.success("删除成功！");
        this.getUserToolsList();
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 获取系统工具配置
  async getSystemTools() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(`${Url.settings.getSystemTools}?orgId=${this.orgId}`);
      if (status) {
        this.checkedSystemData = JSON.parse(data).checkedSystemData;
        this.checkedSystemTools = JSON.parse(data).checkedSystemTools;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  // 保存系统工具配置
  async setSystemTools() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(Url.settings.setSystemTools, {
        orgId: this.orgId,
        toolbarJson: JSON.stringify({
          checkedSystemData: this.checkedSystemData,
          checkedSystemTools: this.checkedSystemTools
        })
      });
      if (status) {
        this.$message.success("保存成功！");
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      return;
    }
  }
  backToList() {
    this.componentStatus = true;
    this.toolItem = {
      isShow: false,
      icons: [],
      typeDataIds: [],
      icon: ""
    };
  }

  created() {
    this.getUserToolsList();
    this.getSystemTools();
  }
}
